      show: false,
      width: "",
    };
  },
  mounted() {
    window.addEventListener("scroll", this.listenHeight);
  },
  methods: {
    listenHeight() {
      let top = document.documentElement.scrollTop;
      this.width =
        (top / (document.body.scrollHeight - window.innerHeight)) * 100;
    },
  },
};
